<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process192"></a>

<p><br></p>
<p><span class="rvts17">堕落的精灵后裔（</span><span class="rvts50">The Fallen Elven Heir</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:618,Y:113</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：加兰（</span><span class="rvts20">Garlen</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">打败精灵后裔加兰</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1224.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在石墓花园，可以找到地图上一处有深坑的地方，有一颗污染的精灵树。在这里使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">，会被瞬间拽到精灵树里面，与加兰遭遇。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1225.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">与加兰的打斗相当有难度，它自带两个腐蚀孢子召唤物，免疫毒元素并通过毒元素可以回血。孢子召唤物是相当厉害的生物，绝对不可掉以轻心。</span>
<br><span class="rvts21">在战胜加兰后，可以在这里其中的一个树桩上，找到一件暗金装备。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



